import { Component } from '@angular/core';

import { InlineEditorDirectives} from 'ng2-inline-editor';

import {
  DATATABLE_COMPONENTS,
  TableOptions,
  TableColumn,
  ColumnMode
} from 'angular2-data-table';


@Component({
  moduleId: module.id,
  selector: 'app-root',
  templateUrl: 'app.component.html',
  directives: [DATATABLE_COMPONENTS, InlineEditorDirectives],

  styleUrls: ['app.component.css']
})
export class AppComponent {
  title = 'angular2-data-table + ng2-inline-editor';
  rows = []
  constructor() {
    this.fetch((data) => {
      this.rows.push(...data);
      console.log(data);
    });

  }

  fetch(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'https://unpkg.com/angular2-data-table@0.2.0/assets/data/company.json');

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }



  options = new TableOptions({
    columnMode: ColumnMode.force,
    headerHeight: 50,
    footerHeight: 50,
    rowHeight: 'auto'
  });

  editableSelectOptions = [
    { value: 'male', text: 'male' },
    { value: 'female', text: 'female' }
  ]


  editableSelectOptionsConfiguration = {
    data: [
      { id: 1, field: 'status1' },
      { id: 2, field: 'status2' },
      { id: 3, field: 'status3' },
      { id: 4, field: 'status4' }
    ],
    value: 'id',
    text: 'field'
  }

  editableSelectOptionsTwoLevelsDefault = 1;
  editableSelectOptionsTwoLevelsConfiguration = {
    data: [
      {
        id: 1, field: 'status1',
        children: [
          { id: 5, field: 'status1.1' },
          { id: 6, field: 'status1.2' }
        ]
      },
      { id: 2, field: 'status2' },
      { id: 3, field: 'status3' },
      {
        id: 4, field: 'status4',
        children: [{ id: 7, field: 'status4.1' }]
      }
    ],
    value: 'id',
    text: 'field'
  }

  fnErrorLength = function (x) {
    alert('This is a custom error!');
  }

  saveEditable(value) {
    //call to http server
    console.log('http.server: ' + value);

  }
}
